import React from 'react'
import type { PrimitiveProps } from '../../types'
import { getformattedProps } from '~/utils/primitives'
import { styleAliases, styleProps, StyledImg } from './styles'

type ImgProps = React.ImgHTMLAttributes<HTMLImageElement> &
  PrimitiveProps<typeof styleProps> & {
    src: string
    alt: string
    loading?: 'lazy' | 'eager'
  }

const Img = ({ src, alt, loading = 'lazy', width, height, css, ...props }: ImgProps) => {
  const { mediaStyles, restProps } = getformattedProps({
    props: { ...props, width, height },
    styleProps,
    styleAliases
  })

  return (
    <StyledImg
      css={{
        ...mediaStyles,
        ...css
      }}
      src={src}
      alt={alt}
      loading={loading}
      {...restProps}
    />
  )
}
export { Img }
